import Helper from '@/function.js';
import mpvueCityPicker from '../../components/mpvue-citypicker/mpvueCityPicker.vue'
import cityData from '../../common/city.data.js';
export default {
	components: {
		mpvueCityPicker
	},
	data() {

		return {
			imgSvr: "",
			token: "",
			info: {},
			array: ['男', '女'],

		}
	},
	onLoad: function() {
		this.imgSvr = this.ImgServer;
		this.token = uni.getStorageSync('token');
		this.getInfo()
	},

	methods: {
		getInfo() {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetInfo',
				data: "",
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {
						if (Helper.isEmpty(res.data.data.sex))
						{
							res.data.data.sex = 0;
						}
						
						if (Helper.isEmpty(res.data.data.userName))
						{
							res.data.data.userName = "";
						}
						
						if (Helper.isEmpty(res.data.data.wechatCode))
						{
							res.data.data.wechatCode = "";
						}
						
						_this.info = res.data.data;
						
						
					} else {

					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}


			})
		},
		saveData() {
			var _this = this;
			
			if (Helper.isEmpty(this.info.avatar))
			{
				uni.showToast({
					title:"请上传头像",
					icon:"none"
				})
				
				return;
			}
			
			if (Helper.isEmpty(this.info.userName))
			{
				uni.showToast({
					title:"请输入姓名",
					icon:"none"
				})
				
				return;
			}
			
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/EditMemberInfo',
				data: _this.info,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {
						uni.showModal({
							title: "信息提示",
							content: "信息修改成功",
							showCancel: false
						})
					} else {
						uni.showModal({
							title: "出错了",
							content: res.data.errorMessage,
							showCancel: false
						})
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}


			})
		},
		/* 性别选择 */
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.info.sex = e.target.value;
		},
		selectPic() {
			var _this = this
			var imgPath = "";
			
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: function(res) {						
					
					imgPath = res.tempFilePaths.toString()
					uni.getImageInfo({
						src: imgPath,
						success: function(image) {
							let canvasWidth = image.width //图片原始长宽
							let canvasHeight = image.height;
							let base = canvasWidth / canvasHeight;
							//设置画布最大宽度
							if (canvasWidth > 500) {
								canvasWidth = 500;
								canvasHeight = Math.floor(canvasWidth / base);
							}
							let img = new Image();
							img.src = imgPath; // 要压缩的图片  
							let canvas = document.createElement('canvas');
							let ctx = canvas.getContext('2d');
							canvas.width = canvasWidth;
							canvas.height = canvasHeight;
							// 清除画布
							ctx.clearRect(0, 0, canvasWidth, canvasHeight);
							//  将图片画到canvas上面   使用Canvas压缩  
							ctx.drawImage(img, 0, 0, canvasWidth, canvasHeight);
							// canvas.toDataURL 返回的是一串Base64编码的URL
							// 指定格式 PNG  
							imgPath = canvas.toDataURL("image/png");
							console.log(imgPath)
							_this.upload(imgPath);
						}
					});
				},
				
			});
		
		},
		
		/* 上传图片 */
		upload(imgurl) {
			//http://localhost:25630
			uni.showLoading({
				title: '请稍候...'
			});
			var _this = this;
		
			uni.uploadFile({
				url: _this.ApiGateWay + '/sale/image/uploadByFile', //仅为示例，非真实的接口地址
				filePath: imgurl, //上传图片的路径
				name: 'file', //对应的key
				formData: {
					path: '/user/avatar/'
				},
				success: res => {
					let _res = JSON.parse(res.data);
					if (_res.errorCode == 0) {
						uni.showToast({
							icon: 'success',
							title: '图片上传成功!'
						});
		
						_this.info.avatar = _res.data;
		
						//
					} else {
						uni.hideLoading();
						uni.showModal({
							title: '提示',
							content: '图片上传失败',
							showCancel: false,
							success: function(res) {}
						});
					}
				},
				complete() {
					_this.upload_field = '';
				}
			});
		}
	}

}
